'use strict';

/**
* Compute the residual sum of squares of two double-precision floating-point strided arrays using an improved Kahan–Babuška algorithm.
*
* @module @stdlib/blas-ext-base-drsskbn
*
* @example
* var Float64Array = require( '@stdlib/array-float64' );
* var drsskbn = require( '@stdlib/blas-ext-base-drsskbn' );
*
* var x = new Float64Array( [ 1.0, -2.0, 2.0 ] );
* var y = new Float64Array( [ 1.0, 1.0, -4.0 ] );
*
* var out = drsskbn( x.length, x, 1, y, 1 );
* // returns 45.0
*
* @example
* var Float64Array = require( '@stdlib/array-float64' );
* var drsskbn = require( '@stdlib/blas-ext-base-drsskbn' );
*
* var x = new Float64Array( [ 1.0, 2.0, 2.0, -7.0, -2.0, 3.0, 4.0, 2.0 ] );
* var y = new Float64Array( [ 2.0, 1.0, 2.0, 1.0, -2.0, 2.0, 3.0, 4.0 ] );
*
* var z = drsskbn.ndarray( x.length, x, 1, 0, y, 1, 0 );
* // returns 72.0
*/

// MODULES //

var join = require( 'path' ).join;
var tryRequire = require( '@stdlib/utils-try-require' );
var isError = require( '@stdlib/assert-is-error' );
var main = require( './main.js' );


// MAIN //

var drsskbn;
var tmp = tryRequire( join( __dirname, './native.js' ) );
if ( isError( tmp ) ) {
	drsskbn = main;
} else {
	drsskbn = tmp;
}


// EXPORTS //

module.exports = drsskbn;

// exports: { "ndarray": "drsskbn.ndarray" }
